import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: true, //空白页现实  true|false
			cartList: [],
		};
	},
	onShow() {
		if (this.hasLogin) {
			this.loadShopCar();
		}
		this.cartList = []
	},
	onLoad() {},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	watch: {
		//显示空白页
		cartList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal();
		},
		//数量
		numberChange(changeNumber, index) {
			this.cartList[index].num = changeNumber;
			this.calcTotal();
		},
		delShopCarGoods(goodDetails) {
			var that = this;
			this.$http.post(this.$httpApi.shopcar.delShopCar, {
				goodDetails
			}, {
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				}
			}).
			then(function(response) {
				//这里只会在接口是成功状态返回
				uni.hideLoading();
				that.loadShopCar();
				that.total = 0;
				that.$api.msg("删除商品成功");
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
			});
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.goodDetailId;
			var that = this;

			uni.showModal({
				title: '提示',
				content: '确认删除该商品？',
				success: function(res) {
					if (res.confirm) {
						var goodDetails = [id];
						that.delShopCarGoods(goodDetails);
					}
				}
			});
		},
		//清空
		clearCart() {
			if (!this.allChecked) {
				return;
			}

			var that = this;
			var index = [];
			uni.showModal({
				content: '清空购物车？',
				success: (e) => {
					if (e.confirm) {
						let list = this.cartList;
						let goodsData = [];
						list.forEach(item => {
							if (item.checked) {
								goodsData.push(item.goodDetailId);
								index.push(item.index);
							}
						});
						that.delShopCarGoods(goodsData);
					}
				}
			})
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.num;
					if (item.pinkAge > 1 && item.num % item.pinkAge > 0) {
						total += item.deliveryFee
						item.totalDeliveryFee = item.deliveryFee
					} else {
						item.totalDeliveryFee = 0
					}
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder() {
			let list = this.cartList;
			let goodsData = [];
			let orderTemp = {};
			let allDeliveryFee = 0;
			list.forEach(item => {
				if (item.checked) {
					goodsData.push({
						goodDetailId: item.goodDetailId,
						num: item.num,
						price: item.price,
						name: item.name,
						imgUrl: item.imgUrl,
						property: item.property,
						pinkAge: item.pinkAge,
						deliveryFee: item.deliveryFee,
						totalDeliveryFee: item.totalDeliveryFee
					})
					allDeliveryFee += item.totalDeliveryFee
				}
			})
			orderTemp["goodsData"] = goodsData;
			orderTemp["totalPrice"] = this.total;
			orderTemp["allDeliveryFee"] = allDeliveryFee;
			orderTemp["orderType"] = 2;

			uni.navigateTo({
				url: `/pages/order/createOrder?data=${JSON.stringify({
					orderTemp: orderTemp
				})}`
			})
		},
		async loadShopCar() {
			var that = this;
			this.$http.get(this.$httpApi.shopcar.showShopCar, {}).
			then(function(response) {
				//这里只会在接口是成功状态返回
				let list = response;
				if (list.size > 0) {
					that.empty = false;
				}
				let cartList = list.map(item => {
					item.checked = true;
					return item;
				});
				that.cartList = cartList;
				that.calcTotal(); //计算总价

			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
			});
		}
	}
}
